//imports
import React from "react";
import { getContinents, getAgeGroups, getCopyRight } from "../..../../../../../../api/entityEnums";
import MultipleSelect from "./MultipleSelect";
import ErrorSnackBar from "../../../../../unsecure/components/ErrorSnackBar";

//MUI import
import { Button, MenuItem, Select } from "@mui/material";

//this function doesn't handle logic, only displaying, elements, and setting the input data
export function StoryForm({
  //props from parent component
  story,
  setStory,
  onSubmit,
  onCancel,
  isNotValidRegisterStory,
  handleErrorCreate
}) {

  //this function handles the events, when data is set by the client
  function handleFormInput(e) {
    //the story state's only data that will change, is the one, whose key matches the currently changing elem
    setStory({
      ...story,
      [e.target.name]: e.target.value
    })
  }

  //inserting elements to the vertual DOM
  //calling the MultipleSelect.jsx child component
  return <div className="addStoryContainer">
    <form className="addStoryForm">
      <div className="rowContainer">
        <label htmlFor="Cm" className="label">Cm</label>
        <label htmlFor="Szrmazsi hely (kontinens)" className="halfLabel">Szrmazsi hely (kontinens)</label><br />
        <input className="halfInput" type="text" id="Cm" name="title" placeholder="Cm.." value={story.title} onChange={handleFormInput} />

        <Select sx={{
          width: '50%',
          backgroundColor: "white",
          height: {
            xs: 29,
            sm: 42
          }
        }} labelId="demo-simple-select-label" id="demo-simple-select" name="continent" value={story.continent} label="continents" onChange={handleFormInput}>
          {getContinents().map(continent => <MenuItem key={continent} value={continent}>{continent.replaceAll('_', ' ')}</MenuItem>)}
        </Select><br />
      </div>

      <div className="rowContainer">
        <MultipleSelect elem={"countries"} story={story} selectElements={story.countries} setStory={setStory} postApiEndpoint={"/api/country/admin/add"} getApiEndpoint={"/api/country"} errorText={"Ez a oszg/kultra mr ltezik"} mainText={"Orszgok/kultrk"} secondaryText={"Adj hozz jat"} />
        <br />
      </div>


      <div className="rowContainer">

        <MultipleSelect elem={"storyBags"} story={story} selectElements={story.storyBags} setStory={setStory} postApiEndpoint={"/api/storyBag/admin/add"} getApiEndpoint={"/api/storyBag"} errorText={"Ez a mesetarisznya mr ltezik"} mainText={"Mesetarisnyk"} secondaryText={"Adj hozz jat"} />
        <br />
      </div>

      <div className="rowContainer">

        <MultipleSelect elem={"keyWords"} story={story} selectElements={story.keyWords} setStory={setStory} postApiEndpoint={"/api/keyWord/admin/add"} getApiEndpoint={"/api/keyWord"} errorText={"Ez a kulcssz mr ltezik"} mainText={"Kulcsszavak"} secondaryText={"Adj hozz jat"} />
        <br />
      </div>

      <div className="rowContainer">
        <label htmlFor="legalStatus" className="label">Joglls</label>
        <label htmlFor="Korosztly" className="halfLabel">Korosztly</label><br />
        <Select sx={{
          width: '50%',
          backgroundColor: "white",
          height: {
            xs: 29,
            sm: 42
          }
        }} labelId="demo-simple-select-label" id="demo-simple-select" name="copyRight" value={story.copyRight} label="Age" onChange={handleFormInput}>
          {getCopyRight().map(copyRight => <MenuItem value={copyRight}>{copyRight.replaceAll('_', ' ')}</MenuItem>)}
        </Select>
        <Select sx={{
          width: '50%',
          backgroundColor: "white",
          height: {
            xs: 29,
            sm: 42
          }
        }} labelId="demo-simple-select-label" id="demo-simple-select" name="ageGroup" value={story.ageGroup} label="Age" onChange={handleFormInput}>
          {getAgeGroups().map(ageGroup => <MenuItem value={ageGroup}>{ageGroup.replaceAll('_', ' ')}</MenuItem>)}
        </Select>
        <br />
      </div>
      <div className="rowContainer">
        <label htmlFor="Forrs" className="label">Forrs</label>
        <br />
        <input className="simpleInput" type="text" id="Forrs" name="source" placeholder="Forrs.." value={story.source} onChange={handleFormInput} />


        <br />
      </div>
      <div className="rowContainer">
        <label htmlFor="Comment" className="label">Megjegyzs</label> <br />
        <input className="simpleInput" type="text" id="comment" name="comment" placeholder="Megjegyzs.." value={story.comment} onChange={handleFormInput} />
        <br />
      </div>
      <div className="rowContainer">
        <label htmlFor="story" className="label">Mese szvege</label> <br />
        <input className="simpleInput" type="text" id="story" name="text" placeholder="Mese szvege.." value={story.text} onChange={handleFormInput} />
        <br />
      </div>
      <div className="buttonContainer">
        <Button sx={{
          top: 5,
          right: 15
        }} color="primary" size="large" variant="contained" onClick={onSubmit}> MENTS</Button>
        <Button sx={{
          top: 5,
          left: 15
        }} color="primary" size="large" variant="contained" onClick={onCancel}> MGSE</Button>
      </div>
    </form>
    <ErrorSnackBar open={isNotValidRegisterStory} handleClose={handleErrorCreate} text={"Ltezik mr ilyen cm, vagy valami nem lett minden kitltve"} />
  </div>;
}
